import axios from "axios"
import QRCode from "qrcode"
import { useEffect, useState } from "react"

const Qr = () => {

    const [ src, setSrc ] = useState('https://imgs.search.brave.com/ik0Nyx5dyvQjZqqU1-o_1hMIUtEyYwbYu5Eqzx1Ii3c/rs:fit:512:512:1/g:ce/aHR0cDovL215cHJl/c3RhLmV1L2NvbnRl/bnQvdXBsb2Fkcy8y/MDEzLzA1L2xvYWRp/bmcucG5n')

    useEffect(()=>{

        const timer = setInterval(() => {
            axios.get('https://696e-103-132-151-80.ngrok.io/api/uid').then( res => {
                QRCode.toDataURL(res.data.uid).then((setSrc))
            })
        }, 15000)
    
        return function stopTimer() {
            clearInterval(timer)
        }

    }, [])

    return(
        <div id="qrcode">
            <img src={src} />
        </div>  
    )
}

export default Qr