import { GoogleAuthProvider, signInWithPopup } from "firebase/auth"
import { auth } from "../Context/firebase-config"

const Login = () => {

    const signInWithGoogle = () => {
        const provider = new GoogleAuthProvider()
        signInWithPopup(auth, provider)
    }

    return(
        <div id="login">
            <button onClick={ signInWithGoogle }>
                <img src="https://imgs.search.brave.com/glot6GX0PbAVOBP1z67py4IcOY2tN-My0l3VGfM6Dpw/rs:fit:474:225:1/g:ce/aHR0cHM6Ly90c2Uy/Lm1tLmJpbmcubmV0/L3RoP2lkPU9JUC5D/YU83ZzBLZURiYzhC/aE1jbE1iZVhnSGFI/YSZwaWQ9QXBp" alt=""/>
                <p>Login</p>
            </button>
        </div>
    )
}

export default Login